
import React, { useEffect} from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { Container, Row, Col } from "react-bootstrap";
import { contactConfig } from "../Navbar/content_option"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope, faPhone } from '@fortawesome/free-solid-svg-icons';
import "../../CSS Files/Contact.css"

gsap.registerPlugin(ScrollTrigger)

function Contact() {

  const slideInLeft = (elem, delay, duration) => {
    gsap.fromTo(
      elem,
      {
        opacity: 0,
        x: -200,
      },
      {
        opacity: 1,
        x: 0,
        delay: delay || 0.2,
        duration: duration || 0.6,
        scrollTrigger: {
          trigger: elem,
          start: "top center",
          end: "bottom center"
        }
      }
    )
  }

  useEffect(() => {
    slideInLeft("#competences");
  }, [])

  return (
    <section id='Contact' className='section-contact'>
    
      <Container className='contact'>
       
      <h1 id="competences" className=' row-alignement text-center titre-competences'>Contact</h1>  
     <Row className='row-alignement row-contact'>
    
     <Col lg="7" md="12" className="align-items-center">
      
       <p className='description-contact mt-30'>{contactConfig.description}<a className='linked'>{contactConfig.linkedin}</a></p>
       
           <strong>
           <FontAwesomeIcon icon={faEnvelope} style={{color: "#ff0000",}} size="2x" />
           <a className='courriel' href={`mailto:${contactConfig.YOUR_EMAIL}`}>
            {contactConfig.YOUR_EMAIL_NOM} {contactConfig.YOUR_EMAIL_EXTENSION} 
           </a>
          
           </strong>
           <br />
           <br />
           {contactConfig.hasOwnProperty("YOUR_FONE") ? (
             <p className='description-contact'>
               <strong><FontAwesomeIcon icon={faPhone} style={{color: "#ff0000",}} size="1x" /></strong> {contactConfig.YOUR_FONE}
             </p>
           ) : (
             ""
           )}
       
        
       </Col>
      
       <Col lg="5"  md="6" className="d-flex align-items-center">
         <form  className="contact__form w-100">
           <Row>
             <Col lg="6" className="form-group">
               <input
                 className="form-control mb-30 form-correction"
                 id="name"
                 name="name"
                 placeholder="Name" 
                 type="text"
                 required 
               />
             </Col>
             <Col lg="6"  md="6" className="form-group">
               <input
                 className="form-control mb-30 form-correction"
                 id="email"
                 name="email"
                 placeholder="Email"
                 type="email" 
                 required 
               />
             </Col>
           </Row>
           <textarea
             className="form-control"
             id="message"
             name="message"
             placeholder="Message"
             rows="5" 
             required
           ></textarea>
           <br />
           <Row>
             <Col lg="12"  md="6"  sm="2" className="form-group">
               <button className="btn ac_btn mb-20" type="submit"> 
               Send
               </button>
             </Col>
           </Row>
         </form>
       </Col> 
      
     </Row>
    
    
   </Container>
    </section>
  );
}

export default Contact;  

