
import React, { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';

import "../../assets/styles/_competences.scss"
import { Container, Row, Col } from "react-bootstrap";
import "../../CSS Files/Contact.css"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faReact,faSquareJs,faSass,faWordpressSimple,faHtml5,faCss3Alt} from "@fortawesome/free-brands-svg-icons";

gsap.registerPlugin(ScrollTrigger)

function Competences() {

  const slideInLeft = (elem, delay, duration) => {
    gsap.fromTo(
      elem,
      {
        opacity: 0,
        x: -200,
      },
      {
        opacity: 1,
        x: 0,
        delay: delay || 0.2,
        duration: duration || 0.6,
        scrollTrigger: {
          trigger: elem,
          start: "top center",
          end: "bottom center"
        }
      }
    )
  }

  useEffect(() => {
    slideInLeft("#competences");
  }, [])
  
    return (
      
      <section id='Competences' className='section-competences'>
        
                 <Container>
        <Row className='justify-content-center row-margin'>
        <h1 id="competences" className='text-center titre-competences'>Compétences</h1>
      
           <Col 
            sm={1}
            className='card_compe'
          >
        
         <FontAwesomeIcon icon={faHtml5} style={{color: "#ebecf3",}} />
       
          </Col>
          <Col 
            sm={1}
            className='card_compe'
          >
        
         <FontAwesomeIcon icon={faCss3Alt} style={{color: "#ebecf3",}} />
       
          </Col>  
            <Col 
            sm={1}
            className='card_compe'
          >
          <FontAwesomeIcon icon={faSquareJs} style={{color: "#ebecf3",}}  />
          </Col> 
          <Col 
            sm={1}
            className='card_compe'
          >
        
         <FontAwesomeIcon icon={faReact} style={{color: "#ebecf3",}} />
       
          </Col>
       
               <Col 
            sm={1}
            className='card_compe'
          >
            <FontAwesomeIcon icon={faSass} style={{color: "#ebecf3",}}  />
          </Col>
           <Col 
            sm={1}
            className='card_compe'
          >
            <FontAwesomeIcon icon={faWordpressSimple} style={{color: "#ebecf3",}}  />
          </Col>
          
        </Row>
      </Container>
      </section>
    );
  }
  
  export default Competences;  