
import {useState,useEffect } from 'react';
import { gsap } from "gsap";
import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectCards } from 'swiper/modules';
import 'swiper/scss';
import 'swiper/css/effect-cards';
import "../../CSS Files/Contact.css"

import  { Col, Row } from 'react-bootstrap';
import "../Projet/project.module.scss"
import terrasseD from "../../assets/images/TERRASSE DIVERSIMMO.png";
import SupraS from "../../assets/images/SUPRA SIGNALISATION.png";
import BillyT from "../../assets/images/billytallentcomplet.png";



function GroupExample() {
  
  const slideInLeft = (elem, delay, duration) => {
    gsap.fromTo(
      elem,
      {
        opacity: 0,
        x: -200,
      },
      {
        opacity: 1,
        x: 0,
        delay: delay || 0.2,
        duration: duration || 0.6,
        scrollTrigger: {
          trigger: elem,
          start: "top center",
          end: "bottom center"
        }
      }
    )
  }

  useEffect(() => {
    slideInLeft("#projet-titre");
  }, [])

    const [infos] = useState([
      {
        title: 'Terrasse Diversimmo',
        text: 'TESTSTSTT',
        image: terrasseD
      },
      {
        title: 'Supra Signalisation',
        text: 'blgjfdkghj',
        image: SupraS
      },  
      {
        title: 'Refonte fictive Les Savoureux',
        text: 'blgjfGFDDDD',
      },  
      {
        title: 'Megazord',
        text: 'blgjfdkgh',
        IMG:'../'
      },  
   

    ])


  return (
    <section id='Projets' className='section-project'>
         <h1  id='projet-titre' className='text-center titre-competences'>Projets</h1>
       <Swiper
        effect={'cards'}
        grabCursor={true}
        modules={[EffectCards]}
        className="mySwiper"
      >
       
        <SwiperSlide>
          <Row xs={1} md={2} className='RowProject'>
            <Col lg="5"  md="6">
             
        <h1 className='description-projet-h1'>Supra Signalisation</h1>
        <p className='description-projets'>Ceci est un exemple de texte car je ne sais pas quoi dire. Un site web statique réalisé avec du simple html css et javascript</p>
            </Col>
            <Col lg="7"  md="6" >
              <div className='container'>
             <img className='image-box' src={SupraS} alt="Supra Signalisation"/>
       </div>
       <div className='container contain-projet-image'>
       <div className='box-bootstrap'><a>Bootstrap</a></div>
       <div className='box-react'><a>React</a></div>
       </div>     
       </Col>
          </Row>
       

          
        </SwiperSlide>
        <SwiperSlide>
        <Row xs={1} md={2} className='RowProject'>
            <Col lg="5"  md="6">
             
        <h1 className='description-projet-h1'>Terrasse Diversimmo</h1>
        <p className='description-projets'>Ceci est un exemple de texte car je ne sais pas quoi dire. Un site web statique réalisé avec du simple html css et javascript</p>
            </Col>
            <Col lg="7"  md="6" >
              <div className='container'>
             <img className='image-box' src={terrasseD} alt="Terrasse Diversimmo"/>
       </div>
       <div className='container contain-projet-image'>
       <div className='box-bootstrap'><a>Bootstrap</a></div>
       <div className='box-react'><a>React</a></div>
       </div>     
       </Col>
          </Row>

          
        </SwiperSlide>
        <SwiperSlide>
        <Row xs={1} md={2} className='RowProject'>
            <Col lg="5"  md="6">
             
        <h1 className='description-projet-h1'>Site Web fictif Billy Talent</h1>
        <p className='description-projets'>Ceci est un exemple de texte car je ne sais pas quoi dire. Un site web statique réalisé avec du simple html css et javascript</p>
            </Col>
            <Col lg="7"  md="6" >
              <div className='container'>
             <img className='image-box' src={BillyT} alt="Supra Signalisation"/>
       </div>
       <div className='container contain-projet-image'>
       <div className='box-bootstrap'><a>Bootstrap</a></div>
       <div className='box-react'><a>React</a></div>
       </div>     
       </Col>
          </Row>

          
        </SwiperSlide>
      
      </Swiper>
   
    </section>
  );
}

export default GroupExample;



